* {
  box-sizing: border-box;
}
.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f8ecd1;
}
.row {
  display: flex;
  background-color: #deb6ab;
  flex-direction: row;
}
.row-item {
  width: 80px;
  height: 70px;
  font-size: 30px;
  padding: 15px 30px;
  cursor: pointer;
  color: #85586f;
}
.bottom-right-bd {
  border-right: 2px solid #ac7d88;
  border-bottom: 2px solid #ac7d88;
}
.right-bd {
  border-right: 2px solid #ac7d88;
}
.bottom-bd {
  border-bottom: 2px solid #ac7d88;
}
.switch-div {
  display: block;
  width: 50px;
  height: 30px;
}
.switch-btn {
  background-color: #5b3749;
  outline: none;
  border: 1px solid #ac7d88;
  color: #ecd2ca;
  border-radius: 5px;
  padding: 6px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 24px;
}

.clear-button-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}
.clear-button {
  margin: 20px 0 0 0;
}
.clear-btn {
  padding: 10px;
  background-color: #5b3749;
  color: #ecd2ca;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  outline: none;
  width: 110px;
}
h1 {
  color: #85586f;
}
.input-box {
  font-size: 16px;
  padding: 10px;
  outline: none;
  border: 1px solid #ac7d88;
  color: #85586f;
  background-color: #f8ecd1;
}
.input-div {
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}
.user-score-div {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  column-gap: 50px;
}
.user-score-head {
  color: #5b3749;
  padding: 5px;
  margin: 5px;
}
.created-info-div {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.created-info-div h4 {
  padding: 3px;
  margin: 3px auto 18px;
  cursor: pointer;
}
@media only screen and (max-width: 600px) {
  .input-box {
    width: 80%;
  }
  .App {
    justify-content: flex-start;
    padding-top: 25px;
  }
  .input-div {
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
  }
}
